<template>
  <div class="login">
    <div class="header">
      <span>蟠龙机动车检测中心</span>
    </div>
    <div class="login-content">
      <div class="login-title">
        <!-- <span class="login-logo"></span> -->
        <!-- 欢迎登录 -->
      </div>
      <AForm @submit.prevent :form="loginForm" class="login-form">
        <!-- <h1 class="form-title">用户登录</h1> -->
        <span class="form-title"><span class="login-logo"></span></span>
        <AFormItem
          required
          class="form-item"
          prop="name"
        >
          <div class="input-box">
            <!-- <span class="input-title">账号：</span> -->
            <div class="input-wrap">
              <AInput placeholder="输入用户名" v-model="loginForm.name" size="large">
                <a-icon slot="prefix" type="user" class="prefix-icon"/>
              </AInput>
            </div>
          </div>
        </AFormItem>
        <AFormItem
          required
          class="form-item password-form"
          prop="pwd"
        >
          <div class="input-box">
            <!-- <span class="input-title">密码：</span> -->
            <div class="input-wrap">
              <AInput type="password" placeholder="输入密码" v-model="loginForm.password" size="large">
                <a-icon slot="prefix" type="key" class="prefix-icon"/>
              </AInput>
            </div>
          </div>
        </AFormItem>
        <div class="warning-tip-wrap" :class="{'error-show': errorTip.show}">
          <span class="error-tip">{{errorTip.message}}</span>
        </div>
        <AFormItem class="btn-wrap">
          <AButton block type="primary" @click="login" class="login-button">登录</AButton>
        </AFormItem>
      </AForm>
    </div>
  </div>
</template>
<script>
import { login } from '@/apis/user-management.js'
// import { SPECIAL_CHARACTERS } from '@/utils/constants.js'
export default {
  name: 'login',
  data () {
    return {
      loginForm: {
        name: '',
        password: ''
      },
      isShowTip: false,
      tipInfo: {
        nameError: false,
        passwordError: false
      },
      errorTip: {
        show: false,
        message: '账户或登录密码不正确'
      }
    }
  },
  methods: {
    login () {
      const isEmpty = this.checkLoginForm()
      if (isEmpty) {
        this.errorTip = {
          show: true,
          message: '账户或登录密码不正确'
        }
        return
      }
      const param = {
        info: JSON.stringify(this.loginForm)
      }
      return login(param).then(res => {
        if (res.success) {
          this.errorTip.show = false
          window.location.href = './'
        } else {
          this.errorTip = {
            show: true,
            message: res.message
          }
        }
      })
      .catch(error => {
        this.errorTip = {
          show: true,
          message: error.message
        }
      })
    },
    checkRule (data) {
      // return data.length < 6 || data.length > 18 || SPECIAL_CHARACTERS.test(data)
      return data.length === 0
    },
    checkLoginForm () {
      let isEmpty = false
      const name = this.loginForm.name.trim()
      const password = this.loginForm.password.trim()
      if (this.checkRule(name) || this.checkRule(password)) {
        isEmpty = true
      }
      return isEmpty
    }
  }
}
</script>
<style lang="less" scoped>
@padding-lg: 25px;
.login {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  background: #f6f4f4;
}
.header {
  font-size: 30px;
  color: #1890ff;
}
.login-form {
  position: relative;
  width: 400px;
  height: 400px;
  padding: 0 @padding-lg;
  border-radius: 4px;
  background: #fff;
  overflow: hidden;
  box-shadow: 0px 0px 6px #999;

  .ant-input {
    background: #2d5eaa;
    padding-left: 35px;
    color: #fff;
    border: none;
    &:focus {
      box-shadow: none;
    }
  }
  .form-title {
    display: inline-block;
    width: 100%;
    font-size: 20px;
    color: #666;
    text-align: center;
    padding: 28px 0 0;
    margin: 0 0 25px;
  }
  .form-item {
    margin-bottom: 40px;
    &:nth-child(4) {
      margin-bottom: 0;
    }
  }
  .password-form {
    margin-bottom: 20px;
  }
  .user-icon {
    display: inline-block;
    width: 16px;
    height: 20px;
    // background: url('../../assets/user.png') no-repeat;
  }
  .pwd-icon {
    display: inline-block;
    width: 16px;
    height: 20px;
    // background: url('../../assets/pwd.png') no-repeat;
  }
}
.login-button {
  width: 300px;
  height: 40px;
  margin-top: 20px;
}
.login-content {
  display: flex;
  flex-direction: column;
}
.change-password {
  display: inline-block;
  color: #c5e5ee;
  margin-bottom: 24px;
  cursor: pointer;
}
.input-box {
  display: flex;
  align-items: center;
  height: 40px;
}
.input-title {
  display: inline-block;
  font-size: 14px;
  width: 50px;
}
.btn-wrap {
  display: flex;
  justify-content: center;
}
.login-title {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: 20px;
  color: #ccc;
  margin-bottom: 20px;
}
.login-logo {
  display: inline-block;
  width: 125px;
  height: 74px;
  background: url('../../assets/logo.jpg') no-repeat;
  background-size: contain;
}
.warning-tip {
  display: flex;
  height: 30px;
  align-items: center;
  margin: 5px 0 0 50px;
}
.error-tip {
  top: 76px;
  left: 24px;
  width: calc(100% - 50px);
  margin: 0;
}
.input-wrap{
  flex: 1;
}
.hide {
  visibility: hidden;
}
.error-tip {
  color: #f00;
}
.prefix-icon {
  font-size: 16px;
}
.warning-tip-wrap {
  visibility: hidden;
}
.error-show {
  visibility: visible;
}
</style>
<style>
.login .warning-tip .ant-alert-icon {
  top: 8px;
}
</style>
